.footer {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: $padding-small;

  @media (max-width: $mobile-width-only) {
    min-height: 694px;
  }

  @media (min-width: $tablet-width) {
    min-height: 368px;
    flex-direction: row;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    min-height: 297px;
  }
}

.footer__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: $mobile-width-only) {
    margin-bottom: 39px;
    padding-top: 20px;
  }

  @media (min-width: $tablet-width) {
    width: 100%;
    margin-bottom: 18px;
    padding-top: 30px;
  }

  @media (min-width: $desktop-width) {
    flex-direction: column;
    max-width: 318px;
    justify-content: flex-start;
    align-items: flex-start;
    padding-left: 20px;
    padding-top: 40px;
  }
}

.footer__wrapper::after {

  @media (max-width: $mobile-width-only) {
    position: absolute;
    content: '';
    top: 80px;
    right: -20px;
    width: 320px;
    height: 1px;
    background-color: $secondary;
  }
}

.footer__logo {
  display: block;

  @media (min-width: $desktop-width) {
    margin-bottom: 9px;
  }
}

.footer__logo-image {
  object-fit: fill;
  cursor: pointer;
  padding-bottom: 2px;

  @media (max-width: $mobile-width-only) {
    width: 83px;
  }

  @media (min-width: $tablet-width) {
    width: 170px;
  }

  @media (min-width: $desktop-width) {}
}

.footer__heading {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.43;
    color: $black;
    margin: 0;
    margin-bottom: 49px;
  }
}

.footer__menu {
  margin-bottom: 29px;

  @media (min-width: $tablet-width) {
    max-width: 223px;
    margin-bottom: 0;
    max-height: 200px;
  }

  @media (min-width: $desktop-width) {
    min-width: 318px;
    padding-top: 50px;
    padding-left: 0;
  }
}

.footer__menu--media {
  @media (min-width: $tablet-width) {
    padding-left: 34px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 0;
  }
}

.footer__menu .footer__menu-title {
  text-align: start;
  text-transform: none;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.43;
  margin: 0;
  margin-bottom: 20px;
  letter-spacing: 0.02em;
}

.footer__menu-list {
  position: relative;
  list-style: none;
  margin: 0;
  padding: 0;
}

.footer__menu-list::after {
  @media (max-width: $mobile-width-only) {
    position: absolute;
    content: '';
    top: 152px;
    right: -20px;
    width: 320px;
    height: 1px;
    background-color: $secondary;
  }
}

.footer__menu-list--media::after {
  @media (max-width: $mobile-width-only) {
    top: 116px;
  }
}

.footer__menu-item {
  min-height: 32px;
  margin-bottom: 4px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

.footer__menu-link {
  text-decoration: none;
  color: $black;
  cursor: pointer;

  &:hover,
  &:focus-visible {
    color: $primary-lighter;
  }

  &:active {
    color: $primary-darker;
  }
}

.footer__menu-address,
.footer__menu-phone {
  position: relative;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.43;
  color: $black;
  text-decoration: none;
  margin: 0;
  padding-left: 19px;
  margin-bottom: 16px;
}

.footer__menu-address::before {
  position: absolute;
  content: url(../img/icon-address.svg);
  top: 2px;
  left: 0px;
}

.footer__menu-phone::before {
  position: absolute;
  content: url(../img/icon-phone.svg);
  top: 1px;
  left: 0px;
}

.footer__menu--contacts {
  margin-bottom: 16px;

  @media (min-width: $tablet-width) {
    max-width: 223px;
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    margin-right: 20px;
  }
}
