.nav__toggle {
  position: relative;
  display: block;
  padding: 30px;
  width: 16px;
  height: 16px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
.nav__toggle:active {
  background-color: #f0ebff;
}

.nav--hidden .nav__toggle::before {
  content: "";
  position: absolute;
  top: 24px;
  left: 22px;
  width: 16px;
  height: 2px;
  background-color: #333333;
  box-shadow: 0 5px 0 0 #333333, 0 10px 0 0 #333333;
}

.nav--visible .nav__toggle::before, .nav--visible .nav__toggle::after {
  content: "";
  position: absolute;
  top: 29px;
  left: 22px;
  width: 16px;
  height: 2px;
  background-color: #333333;
}
.nav--visible .nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.nav--visible .nav__toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .nav--hidden .site-list {
    display: none;
